import React from 'react';
import PropTypes from 'prop-types';
import '@contentful/forma-36-react-components/dist/styles.css';
import './index.css';
import SimpleMDE from "react-simplemde-editor";
import "codemirror";
import "codemirror/addon/search/searchcursor";
import "codemirror/addon/search/search";
import "easymde/dist/easymde.min.css";


export default class Editor extends React.Component {
  static propTypes = {
    value: PropTypes.object.isRequired,
    sdk: PropTypes.object.isRequired,
    onChange: PropTypes.func.isRequired,
  };

  detachExternalChangeHandler = null;

  constructor(props) {
    super(props);
    this.state = {
      value: props.value,
    };
  }

  getInstance = instance => {
    // codemirror extensions
    instance.codemirror.options.extraKeys["Cmd-F"] = "findPersistent";
  };

  /**
 * The state of CodeMirror at the given position.
 */
  getState(cm, pos) {
    pos = pos || cm.getCursor('start');
    var stat = cm.getTokenAt(pos);
    if (!stat.type) return {};

    var types = stat.type.split(' ');

    var ret = {},
      data, text;
    for (var i = 0; i < types.length; i++) {
      data = types[i];
      if (data === 'strong') {
        ret.bold = true;
      } else if (data === 'variable-2') {
        text = cm.getLine(pos.line);
        if (/^\s*\d+\.\s/.test(text)) {
          ret['ordered-list'] = true;
        } else {
          ret['unordered-list'] = true;
        }
      } else if (data === 'atom') {
        ret.quote = true;
      } else if (data === 'em') {
        ret.italic = true;
      } else if (data === 'quote') {
        ret.quote = true;
      } else if (data === 'strikethrough') {
        ret.strikethrough = true;
      } else if (data === 'comment') {
        ret.code = true;
      } else if (data === 'link') {
        ret.link = true;
      } else if (data === 'tag') {
        ret.image = true;
      } else if (data.match(/^header(-[1-6])?$/)) {
        ret[data.replace('header', 'heading')] = true;
      }
    }
    return ret;
  }

  _replaceSelection(cm, active, startEnd, url) {
    if (/editor-preview-active/.test(cm.getWrapperElement().lastChild.className))
      return;

    var text;
    var start = startEnd[0];
    var end = startEnd[1];
    var startPoint = {},
      endPoint = {};
    Object.assign(startPoint, cm.getCursor('start'));
    Object.assign(endPoint, cm.getCursor('end'));
    if (url) {
      end = end.replace('#url#', url);
    }
    if (active) {
      text = cm.getLine(startPoint.line);
      start = text.slice(0, startPoint.ch);
      end = text.slice(startPoint.ch);
      cm.replaceRange(start + end, {
        line: startPoint.line,
        ch: 0,
      });
    } else {
      text = cm.getSelection();
      cm.replaceSelection(start + text + end);

      startPoint.ch += start.length;
      if (startPoint !== endPoint) {
        endPoint.ch += start.length;
      }
    }
    cm.setSelection(startPoint, endPoint);
    cm.focus();
  }

  onAddImage(props) {
    return async (editor) => {
      const asset = await props.sdk.dialogs.selectSingleAsset();
      if (asset) {
        var cm = editor.codemirror;
        var stat = this.getState(cm);
        var options = editor.options;
        var url = `https:${asset.fields.file.en.url}`;
        this._replaceSelection(cm, stat.image, options.insertTexts.image, url);
      }
    }
  }

  render() {
    return (
      <SimpleMDE
        onChange={this.props.onChange}
        value={this.state.value}
        getMdeInstance={this.getInstance}
        options={{
          toolbar: [
            "bold", "italic", "heading", "|",
            "quote", "unordered-list", "ordered-list", "|",
            "strikethrough", "code", "horizontal-rule", "|",
            "link", {
              name: "contentful-image",
              action: this.onAddImage(this.props),
              className: "fa fa-picture-o",
              title: "Insert Image",
            }, "preview", {
              name: "full-screen",
              action: function (editor) {
                editor.toggleFullScreen();
                const isFullscreen = document.webkitIsFullScreen || document.mozFullScreen || false;
                isFullscreen ? document.exitFullscreen() : document.body.requestFullscreen();
              },
              className: "fa fa-arrows-alt no-disable no-mobile",
              title: "Full Screen",
            }
          ]
        }}
      />
    );
  }
}
